'use client'
import { useRef } from 'react'
import { Group } from 'three'
import { Particles } from './Particles'
import { Signals } from './Signals'
import { Beams } from './Beams'
import { Connections } from './Connections'
import { useAnimationController } from './AnimationController'
import type { ParticlesRef, SignalsRef, BeamsRef, ConnectionsRef } from './types'

export default function NeuralNetwork() {
  const particles = useRef<ParticlesRef>(null)
  const signals = useRef<SignalsRef>(null)
  const beams = useRef<BeamsRef>(null)
  const connections = useRef<ConnectionsRef>(null)

  useAnimationController({ particles, signals, beams, connections })

  return (
    <Group>
      <Particles ref={particles} />
      <Signals ref={signals} />
      <Beams ref={beams} />
      <Connections ref={connections} />
    </Group>
  )
}