'use client'
import { useRef, useMemo } from 'react'
import * as THREE from 'three'
import signalVert from './shaders/signal.vert'
import signalFrag from './shaders/signal.frag'

export function Signals() {
  const signals = useRef<THREE.InstancedMesh>(null!)
  const dummy = useMemo(() => new THREE.Object3D(), [])
  
  const material = useMemo(() => new THREE.ShaderMaterial({
    uniforms: {
      time: { value: 0 },
      intensity: { value: 1.0 }
    },
    vertexShader: signalVert,
    fragmentShader: signalFrag,
    transparent: true,
    blending: THREE.AdditiveBlending
  }), [])

  return (
    <instancedMesh
      ref={signals}
      args={[undefined, undefined, 150]}
      material={material}
    >
      <sphereGeometry args={[1.2, 32, 32]} />
    </instancedMesh>
  )
}